import React,{Component} from 'react';
import SidebarMenu from '../component/SidebarMenu'; 
import Footer from '../component/Footer'; 
import Topbar from '../component/Topbar'; 
import Content from '../component/Content'; 
import Smallchat from '../component/Smallchat';
import TopbarNotice from '../component/TopbarNotice';
import axios from 'axios';
import swal from 'sweetalert';
class Role extends React.Component{

    constructor(){
        super();
        this.state={
           
            name:"",
            role:[]
        }
    }
/*   refreshPage(){
		window.location.reload(true);
	}  */
    handleChange = event => {
        var inputName=event.target.name;
        var inputValue=event.target.value;
        this.setState({[inputName]:inputValue});
    
      }
    /* get data  */
    componentDidMount(){

            /* get timesheet data  */

        // axios.get("http://localhost/idb_laravel/my_project/api/photos")  
        axios.get("https://react.mamunforever.com/api/role_get.php")  
        .then(response=>{
            console.log(response.data)
         
           this.setState({role:response.data})
       
        })
        .catch(error=>{
            console.log(error)
        })



    }


    handleSubmit = event => {

        event.preventDefault();
    
       
      
        //axios.get(`http://localhost/api/api.php`)
      axios.post(`https://react.mamunforever.com/api/role_post.php`, this.state)
      /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
        .then(response => {
      
            console.log(response.data)
            /* this.filter = response.data
         */
            })
            .catch(e => {
            //this.errors.push(e)
            })
      }

     
    
    render(){
       
   

        const role_list=this.state.role.map((data)=>{
            return <tr>
            <td>
            {data.id}
            </td>
            <td>
           { data.name}
            </td>
                   
            <td>
                <span class="label label-primary">Enable</span>
            </td>
            <td class="text-right">
                <div class="btn-group">
                    <button class="btn-white btn btn-xs">View</button>
                    <button class="btn-white btn btn-xs">Edit</button>
                </div>
            </td>
        </tr>
      

            })
        return(
            <>

<div id="wrapper">   
                  <SidebarMenu/>
        <div id="page-wrapper" class="gray-bg dashbard-1">
                 <Topbar/>
                 <div class="row wrapper border-bottom white-bg page-heading">
<div class="col-lg-10">
    <h2>Role</h2>
    <ol class="breadcrumb">
        <li class="breadcrumb-item">
            <a href="index.html">Home</a>
        </li>
      
        <li class="breadcrumb-item active">
            <strong>Role</strong>
        </li>
    </ol>
</div>
<div class="col-lg-2">

</div>
</div>

<div class="wrapper wrapper-content animated fadeInRight ecommerce">


<div class="ibox-content m-b-sm border-bottom">
<div class="row">
  
    <div class="col-sm-6">
        <div class="form-group">
        <label class="col-form-label" for="product_name">Search</label>
            <input type="text" id="product_name" name="product_name" value="" placeholder="Searching................" class="form-control"/>
        </div>
    </div>
   
    <div class="col-sm-6">
        <div class="form-group" style={{marginTop:"7%",float:"right"}}>         
        
            <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#modalCart">Add Role</button>
        </div>
    </div>
</div>

</div>

<div class="row">
<div class="col-lg-12">
    <div class="ibox">
        <div class="ibox-content">

            <table class="footable table table-stripped toggle-arrow-tiny" data-page-size="15">
                <thead>
                <tr>

                    <th data-toggle="true">Id</th>
                    <th data-hide="phone">Name</th>
                   
                   
                    <th data-hide="phone">Status</th>
                    <th class="text-right" data-sort-ignore="true">Action</th>

                </tr>
                </thead>
                <tbody>

                {role_list}

                </tbody>
               
               
                <tfoot>
                <tr>
                    <td colspan="6">
                        <ul class="pagination float-right"></ul>
                    </td>
                </tr>
                </tfoot>
            </table>

        </div>
    </div>
</div>
</div>


</div>



{/* modal  */}
 {/*  <!-- Button trigger modal--> */}


{/* <!-- Modal: modalCart --> */}
<div class="modal fade" id="modalCart" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
  aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
    {/*   <!--Header--> */}
      <div class="modal-header">
        <h4 class="modal-title" id="myModalLabel">Add Role</h4>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">×</span>
        </button>
      </div>
     {/*  <!--Body--> */}
      <div class="modal-body"> 
        <form onSubmit={this.handleSubmit}>                    
     
                <div class="form-group">            
                      <label for="name">Name</label>
                       <input type="text" class="form-control" id="name" onChange={this.handleChange} name="name" placeholder="Enter your name"/>
                </div>               


                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-primary" data-dismiss="modal">Close</button>
                    <button type="submit"class="btn btn-primary" onClick={this.refreshPage}>send</button>
                 </div>
            </form>
      </div>

     {/*  <!--Footer--> */}
     
    </div>
  </div>
</div>
{/* <!-- Modal: modalCart --> */}


                 <Footer/>
        </div>
                <Smallchat/>
                <TopbarNotice/>        
    </div>


</>
            )
        }
    }
    export default Role;