const SidebarDropDownLink=(props)=>{

    return(
        <li>
     <a href="#"><i class={props.classIcon}></i> <span class="nav-label">{props.name}</span><span class="fa arrow"></span></a>
        <ul class="nav nav-second-level collapse">
            {/* <li><a href="graph_flot.html">User</a></li> */}
            {      
            props.items.map((item)=>(
                    
              <SidebarLink route={item.route} name={item.name} classIcon={item.classIcon} active={item.active} />
  
            ))
          }
            
        </ul>
    </li>



     
    )
  
  }

  const SidebarLink=(props)=>{

    return(

        <li class={props.active}>
        <a href={props.route}><i class={props.classIcon}></i> <span class="nav-label"> {props.name}</span> </a>
       
    </li>


  
    )
  
  }
