import React, { Component } from 'react';
import SidebarMenu from '../component/SidebarMenu';
import Footer from '../component/Footer';
import Topbar from '../component/Topbar';
import Content from '../component/Content';
import Smallchat from '../component/Smallchat';
import TopbarNotice from '../component/TopbarNotice';
import axios from 'axios';
class Employee_salary extends React.Component {

    constructor() {
        super();
        this.state = {
            employee_id:"",
            user_id: "",
            payment_date: "",
            month_id: "",
            payment_method: "",
            overtime: "",
            absent: "",
            basic_salary: "",
            net_salary: "",         
            remark: "",
            perhour: "",
            day_amount: "",
            overtime_amount: "",
            absent_amount: "",
            employee_salary: [],         
            employee: [],
            users: [],
            month_list: []
        }
    }
    //  refreshPage(){
    // 		window.location.reload(true);
    // 	}
    handleChange = event => {
        var inputName = event.target.name;
        var inputValue = event.target.value;
        this.setState({ [inputName]: inputValue });
        // var emid=this.state.employee_id;
        //  console.log(this.emid)



    }
    getmonth = event=>{
        let m_id = event.target.value;
        this.setState({ month_id: m_id });
        
      console.log(event)
      
    }
    getsalary = event => {

        console.log(this.state.month_id)
        
        let e_id = event.target.value;
       
      //  console.log(e)
        this.setState({ employee_id: e_id });
        let json = { 'id': e_id };
        /*   console.log(this.state.leave_type_id)
          console.log(this.state.employee_id) */
        axios.post(`https://react.mamunforever.com/api/employee/absent_get.php`, json)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {

                //  console.log(response.data*this.state.day_amount)
                this.filter = response.data
                /* window.location.reload(); */
                this.setState({ absent: response.data })
                // console.log(this.state.absent)
            })
            .catch(e => {
                //this.errors.push(e)
            })

        let ojson = { 'id': e_id };

        axios.post(`https://react.mamunforever.com/api/employee/overtime_get.php`, ojson)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {

                // console.log(response.data)
                //   console.log(response.data*2*this.state.perhour)
                this.filter = response.data
                this.setState({ overtime: response.data })
                /* window.location.reload(); */
                //    console.log(this.state.overtime)
            })
            .catch(e => {
                //this.errors.push(e)
            })


        let nsjson = { 'id': e_id,'m_id': this.state.month_id};

        axios.post(`https://react.mamunforever.com/api/net_salary_get.php`, nsjson)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {


                console.log(response.data)
               
                this.filter = response.data
                this.setState({net_salary:response.data })
          


            })
            .catch(e => {
                //this.errors.push(e)
            })


    }


    /* get data  */
    componentDidMount() {

        // axios.get("http://localhost/idb_laravel/my_project/api/photos")  
        axios.get("https://react.mamunforever.com/api/employee_salary_get.php")
            .then(response => {
                console.log(response.data)

                /* console.log(response.status) */
                this.setState({ employee_salary: response.data })

            })
            .catch(error => {
                console.log(error)
            })


        // axios.get("http://localhost/idb_laravel/my_project/api/photos")  
        axios.get("https://react.mamunforever.com/api/month_list_get.php")
            .then(response => {
                console.log(response.data)

                /* console.log(response.status) */
                this.setState({ month_list: response.data })

            })
            .catch(error => {
                console.log(error)
            })









        /* get leave type data  */

        // axios.get("http://localhost/idb_laravel/my_project/api/photos")  
        axios.get("https://react.mamunforever.com/api/users_get.php")
            .then(response => {
                console.log(response.data)
                console.log(response.employee_id)
                /*   console.log(response.data)
                 console.log(response.status) */
                this.setState({ users: response.data })

            })
            .catch(error => {
                console.log(error)
            })

        /* manage leave list*/
        axios.get("https://react.mamunforever.com/api/leave_get.php")
            .then(response => {
                console.log(response.data)
                console.log(response.employee_id)
                /*   console.log(response.data)
                 console.log(response.status) */
                this.setState({ leave: response.data })

            })
            .catch(error => {
                console.log(error)
            })


        /* get employee data */
        axios.get("https://react.mamunforever.com/api/employee/employee_get.php")
            .then(response => {
                console.log(response.data)

                /*   console.log(response.data)
                 console.log(response.status) */

                this.setState({ employee: response.data })
            })
            .catch(error => {
                console.log(error)
            })



    }


    handleSubmit = event => {

        event.preventDefault();

        //axios.get(`http://localhost/api/api.php`)
        axios.post(`https://react.mamunforever.com/api/employee_salary_post.php`, this.state)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {
                console.log(response)
                console.log(response.data)
                this.filter = response.data

            })
            .catch(e => {
                //this.errors.push(e)
            })
    }



    render() {


      
        var employee_id = this.state.employee.map((edata) => {

            return <option value={edata.id}>{edata.name}</option>
        })

        var month_list = this.state.month_list.map((edata) => {

            return <option value={edata.id}>{edata.name}</option>
        })
        var user_list = this.state.users.map((edata) => {

            return <option value={edata.id}>{edata.name}</option>
        })

        const my = this.state.employee_salary.map((data) => {
            return <tr>
                <td>
                    {data.id}
                </td>
                <td>
                    {data.employee_id}
                </td>
                <td>
                    {data.user_id}
                </td>
                <td>
                    {data.bill_make_date}
                </td>
                <td>
                    {data.month_id}
                </td>
                <td>
                    {data.payment_method}
                </td>
                <td>
                    {data.net_salary}
                </td>
                <td>
                    {data.status}
                </td>
                <td>
                    {/*   <span class="label label-primary">Enable</span> */}
                </td>
                <td class="text-right">
                    <div class="btn-group">
                        <button class="btn-white btn btn-xs">View</button>
                        <button class="btn-white btn btn-xs">Edit</button>
                    </div>
                </td>
            </tr>


        })
        return (
            <>

                <div id="wrapper">
                    <SidebarMenu />
                    <div id="page-wrapper" class="gray-bg dashbard-1">
                        <Topbar />
                        <div class="row wrapper border-bottom white-bg page-heading">
                            <div class="col-lg-10">
                                <h2>Employee Salary</h2>
                                <ol class="breadcrumb">
                                    <li class="breadcrumb-item">
                                        <a href="index.html">Home</a>
                                    </li>

                                    <li class="breadcrumb-item active">
                                        <strong>Employee Salary</strong>
                                    </li>
                                </ol>
                            </div>
                            <div class="col-lg-2">

                            </div>
                        </div>

                        <div class="wrapper wrapper-content animated fadeInRight ecommerce">


                            <div class="ibox-content m-b-sm border-bottom">
                                <div class="row">
                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <label class="col-form-label" for="product_name">Search</label>
                                            <input type="text" id="product_name" name="product_name" value="" placeholder="Searching........." class="form-control" />
                                        </div>
                                    </div>
     

                                    <div class="col-sm-6">
                                        <div class="form-group" style={{marginTop:"7%",float:"right"}}>                                      
        
                                            <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#modalCart">add salary</button>
                                        </div>
                                    </div>
                                </div>

                            </div>

                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="ibox">
                                        <div class="ibox-content">

                                            <table class="footable table table-stripped toggle-arrow-tiny" data-page-size="15">
                                                <thead>
                                                    <tr>

                                                        <th data-toggle="true">Id</th>
                                                        <th data-hide="phone">Employee Name</th>
                                                        <th data-hide="all">User Name</th>
                                                        <th data-hide="all">Bill date</th>
                                                        <th data-hide="all">Salary Month</th>
                                                        <th data-hide="all">Payment Method</th>
                                                        <th data-hide="all">Net Salary</th>
                                                        <th data-hide="all">status</th>
                                                        <th class="text-right" data-sort-ignore="true">Action</th>

                                                    </tr>
                                                </thead>
                                                <tbody>

                                                    {my}

                                                </tbody>


                                                <tfoot>
                                                    <tr>
                                                        <td colspan="6">
                                                            <ul class="pagination float-right"></ul>
                                                        </td>
                                                    </tr>
                                                </tfoot>
                                            </table>

                                        </div>
                                    </div>
                                </div>
                            </div>


                        </div>



                        {/* modal  */}
                        {/*  <!-- Button trigger modal--> */}


                        {/* <!-- Modal: modalCart --> */}
                        <div class="modal fade" id="modalCart" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
                            aria-hidden="true">
                            <div class="modal-dialog" role="document">
                                <div class="modal-content">
                                    {/*   <!--Header--> */}
                                    <div class="modal-header">
                                        <h4 class="modal-title" id="myModalLabel">Your cart</h4>
                                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                            <span aria-hidden="true">×</span>
                                        </button>
                                    </div>
                                    {/*  <!--Body--> */}
                                    <div class="modal-body">
                                        <form onSubmit={this.handleSubmit}>
                                        <div class="form-group">
                                                <label for="employee">Month name</label>
                                                <select class="form-control" onChange={this.handleChange} onChange={(e) => this.getmonth(e)} name="month_id">
                                                    <option>select Month</option>
                                                    {month_list}

                                                </select>
                                            </div>
                                            <div class="form-group">
                                                <label for="employee">Employee Name</label>
                                                {/*     <input type="text" class="form-control" id="employee" placeholder="Example input"/> */}
                                                <select class="form-control" onChange={this.handleChange} onChange={(e) => this.getsalary(e)} name="employee_id">
                                                    <option>  select employee     </option>

                                                    {employee_id}

                                                </select>

                                            </div>
                                            <div class="form-group">
                                                <label for="employee">User name</label>

                                                <select class="form-control" onChange={this.handleChange} name="user_id">
                                                    <option>select user</option>

                                                    {user_list}

                                                </select>
                                             </div>
                                           


                                            <div class="form-group">
                                                <label for="bill_make_date">bill Date</label>
                                                <input type="date" class="form-control" onChange={this.handleChange} name="bill_make_date" id="bill_make_date" />
                                            </div>
                                            <div class="form-group">

                                                <label for="net">net salary</label>
                                                <input type="text" class="form-control" id="net" onChange={this.handleChange} name="net_salary" value={this.state.net_salary} placeholder="net_salary" />
                                            </div>

                                            <div class="modal-footer">
                                                <button type="button" class="btn btn-outline-primary" data-dismiss="modal">Close</button>
                                                <button type="submit" class="btn btn-primary" onClick={this.refreshPage}>send</button>
                                            </div>
                                        </form>
                                    </div>

                                    {/*  <!--Footer--> */}

                                </div>
                            </div>
                        </div>
                        {/* <!-- Modal: modalCart --> */}


                        <Footer />
                    </div>
                    <Smallchat />
                    <TopbarNotice />
                </div>


            </>
        )
    }
}
export default Employee_salary;