import React, { Component } from 'react';
import SidebarMenu from '../component/SidebarMenu';
import Footer from '../component/Footer';
import Topbar from '../component/Topbar';
import Content from '../component/Content';
import Smallchat from '../component/Smallchat';
import TopbarNotice from '../component/TopbarNotice';
import axios from 'axios';
class Overtime extends React.Component {

    constructor() {
        super();
        this.state = {
            employee_id: "",
            overtime_date: "",
            hours: "",
            description: "",
            log:false,
            daily:true,
            monthly:false,
            yearly:false,
            searche:[],
            country: [],
            employee: []
        }
    }
    //  refreshPage(){
    // 		window.location.reload(true);
    // 	}
    handleSearch=event=>{
        let src=event.target.value;
       this.search(src);
    }
    handleClick=e=>{

        this.search(e);
        if(e=="monthly"){
            this.setState({ monthly:true })
            this.setState({ daily:false })
            this.setState({ yearly:false })

        }else if(e=="yearly"){
            this.setState({ daily:false })
            this.setState({ monthly:false })
            this.setState({ yearly:true })
        }else if(e=="daily"){
            this.setState({ daily:true })
            this.setState({ monthly:false })
            this.setState({ yearly:false })
        }else{
            this.setState({ daily:true })
            this.setState({ monthly:false })
            this.setState({ yearly:false })
        }
       
    }
     search=(src)=>{
       
        console.log(src);
      let json={'id':src};
        axios.get("https://react.mamunforever.com/api/search.php?res="+ src)
        /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
        .then(response => {
         //   console.log( this.filter.s = response.data)
         this.setState({ searche: response.data })
            console.log(this.state.searche)
            this.setState({ log:response.data })
            src.this.filter = response.data
/* window.location.reload(); */
        })
        .catch(e => {
            //this.errors.push(e)
        })
     }
    handleChange = event => {

        var inputName = event.target.name;
        var inputValue = event.target.value;
        this.setState({ [inputName]: inputValue });

    }

    /* get data  */
    componentDidMount() {

      

        // axios.get("http://localhost/idb_laravel/my_project/api/photos")  
        this.search("");

        /* get employee data */
        axios.get("https://react.mamunforever.com/api/employee/employee_get.php")
            .then(response => {
     
                /*   console.log(response.data)
                 console.log(response.status) */
                this.setState({ employee: response.data })
            })
            .catch(error => {
                console.log(error)
            })




    }


    handleSubmit = event => {

        event.preventDefault();




        //axios.get(`http://localhost/api/api.php`)
        axios.post(`https://react.mamunforever.com/api/overtime_post.php`, this.state)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {
           //     console.log(response)
           //     console.log(response.data)
                this.filter = response.data

            })
            .catch(e => {
                //this.errors.push(e)
            })
    }



    render() {
  
        var employee_id = this.state.employee.map((edata) => {

            return <option value={edata.id}>{edata.name}</option>
        })

      /*   var search_list= this.state.searche.map((data) => {
           
            return  <p>ok</p>
        }) */
        const my = this.state.searche.map((data) => {
            return <tr>
                <td>
                    {data.id}
                </td>
                <td>
                    {data.employee_id}
                </td>
                <td>
                    {data.overtime_date}
                </td>
                <td>
                    {data.hours}
                </td>
                <td>
                    {data.description}
                </td>
                <td>
                    <span class="label label-primary">Enable</span>
                </td>
                <td class="text-right">
                    <div class="btn-group">
                        <button class="btn-white btn btn-xs">View</button>
                        <button class="btn-white btn btn-xs">Edit</button>
                    </div>
                </td>
            </tr>


        })
        return (
            <>

                <div id="wrapper">
                    <SidebarMenu />
                    <div id="page-wrapper" class="gray-bg dashbard-1">
                        <Topbar />
                        <div class="row wrapper border-bottom white-bg page-heading">
                            <div class="col-lg-10">
                                <h2>Overtime</h2>
                                <ol class="breadcrumb">
                                    <li class="breadcrumb-item">
                                        <a href="index.html">Home</a>
                                    </li>

                                    <li class="breadcrumb-item active">
                                        <strong>Overtime</strong>
                                    </li>
                                </ol>
                            </div>
                            <div class="col-lg-2">

                            </div>
                        </div>

                        <div class="wrapper wrapper-content animated fadeInRight ecommerce">


                            <div class="ibox-content m-b-sm border-bottom">
                                <div class="row">
                                   
                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <label class="col-form-label" for="search">search</label>
                                            {/* <input type="text"  name="search"  value="" placeholder="Price" class="form-control" /> */}
                                            <input type="text" id="search" onChange={(e)=>this.handleSearch(e)} class="form-control" placeholder="Searching........."/>
                                        </div>
                                    </div>

                                    <div class="col-sm-6">
                                        <div class="form-group" style={{marginTop:"7%",float:"right"}}>
                                        
                                            <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#modalCart">Add Overtime</button>
                                      {/*       <button type="button" class="btn btn-primary" onClick={(e)=>this.handleClick("daily")}>all</button>
                                            <button type="button" class="btn btn-primary" onClick={(e)=>this.handleClick("monthly")}>monthly</button>
                                            <button type="button" class="btn btn-primary" onClick={(e)=>this.handleClick("yearly")}>yearly</button> */}
                                        </div>
                                    </div>
                                </div>

                            </div>

                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="ibox">
                                        <div class="ibox-content">

                                            <table class="footable table table-stripped toggle-arrow-tiny" data-page-size="15">
                                                <thead>
                                                    <tr>

                                                        <th data-toggle="true">Id</th>
                                                        <th data-hide="phone">Employee Name</th>
                                                        <th data-hide="all">Overtime date</th>
                                                        <th data-hide="all">Hours</th>
                                                        <th data-hide="all">remark</th>

                                                        <th data-hide="phone">Status</th>
                                                        <th class="text-right" data-sort-ignore="true">Action</th>

                                                    </tr> 
                                                </thead>
                                                {this.state.daily?
                                                
                                                   <tbody id="daily">                                                  
                                                  {my}
                                                 </tbody>:null
                                            }
                                              {
                                              
                                              this.state.yearly?
                                                
                                                <tbody id="monthly">                                                  
                                                   <p>yearly</p>
                                              </tbody>:null 
                                         }

                                              {
                                              
                                              this.state.monthly?
                                                
                                                <tbody id="monthly">                                                  
                                                   {my}
                                              </tbody>:null 
                                         }   

                                                <tbody id="monthly">                                                  
                                                    {/* {my} */}
                                                </tbody>
                                                <tbody id="yearly">                                                  
                                                  {/*   {my} */}
                                                </tbody>

                                                <tfoot>
                                                    <tr>
                                                        <td colspan="6">
                                                            <ul class="pagination float-right"></ul>
                                                        </td>
                                                    </tr>
                                                </tfoot>
                                            </table>

                                        </div>
                                    </div>
                                </div>
                            </div>


                        </div>



                        {/* modal  */}
                        {/*  <!-- Button trigger modal--> */}


                        {/* <!-- Modal: modalCart --> */}
                        <div class="modal fade" id="modalCart" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
                            aria-hidden="true">
                            <div class="modal-dialog" role="document">
                                <div class="modal-content">
                                    {/*   <!--Header--> */}
                                    <div class="modal-header">
                                        <h4 class="modal-title" id="myModalLabel">Your cart</h4>
                                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                            <span aria-hidden="true">×</span>
                                        </button>
                                    </div>
                                    {/*  <!--Body--> */}
                                    <div class="modal-body">
                                        <form onSubmit={this.handleSubmit}>
                                            <div class="form-group">
                                                <label for="employee">Employee Name</label>
                                                {/*     <input type="text" class="form-control" id="employee" placeholder="Example input"/> */}
                                                <select class="form-control" onChange={this.handleChange} name="employee_id">
                                                    <option>
                                                        select employee
               </option>

                                                    {employee_id}

                                                </select>
                                            </div>

                                            <div class="form-group">

                                                <label for="date">Date</label>
                                                <input type="date" class="form-control" onChange={this.handleChange} name="overtime_date" id="date" />
                                            </div>
                                            <div class="form-group">

                                                <label for="Hours">Hours</label>
                                                <input type="text" class="form-control" id="Hours" onChange={this.handleChange} name="hours" placeholder="Hours" />
                                            </div>
                                            <div class="form-group">

                                                <label for="formGroupExampleInput">Description</label>
                                                <textarea type="text" class="form-control" onChange={this.handleChange} name="description" placeholder="description" />
                                            </div>


                                            <div class="modal-footer">
                                                <button type="button" class="btn btn-outline-primary" data-dismiss="modal">Close</button>
                                                <button type="submit" class="btn btn-primary" onClick={this.refreshPage}>send</button>
                                            </div>
                                        </form>
                                    </div>

                                    {/*  <!--Footer--> */}

                                </div>
                            </div>
                        </div>
                        {/* <!-- Modal: modalCart --> */}


                        <Footer />
                    </div>
                    <Smallchat />
                    <TopbarNotice />
                </div>


            </>
        )
    }
}
export default Overtime;