import React, { Component } from 'react';
import SidebarMenu from '../component/SidebarMenu';
import Footer from '../component/Footer';
import Topbar from '../component/Topbar';
import Content from '../component/Content';
import Smallchat from '../component/Smallchat';
import TopbarNotice from '../component/TopbarNotice';
import axios from 'axios';
class Overtime extends React.Component {

    constructor() {
        super();

        this.state = {
            employee_id: "",
            leave_type_id: "",
            start_date: "",
            end_date: "",
            leave_reason: "",
            remark: "",
            show:"",            
            auto:"Select",
            leave: [],
            employee: [],
            leave_type: []
        }
    }
    //  refreshPage(){
    // 		window.location.reload(true);
    // 	}
    stattement = value => {
        if (value == 0) {
            return <span class="label label-primary">Pending</span>;
        } else if (value == 1) {

            return <span class="label label-success">Aproval</span>;
        } else {
            return <span class="label label-danger">Reject</span>;
        }
    }

    handleStatus =(event,id)=> {
       
   var SelectedValue=event.target.value;
   this.setState({show:SelectedValue,auto:SelectedValue})
   let json={'id':this.state.show,eid:id};

            axios.post(`https://react.mamunforever.com/api/leave_edit.php`, json)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {
                console.log(response)
                console.log(response.data)
                this.filter = response.data
/* window.location.reload(); */
            })
            .catch(e => {
                //this.errors.push(e)
            })


  
    }
    handleChange = event => {
        var inputName = event.target.name;
        var inputValue = event.target.value;
        this.setState({ [inputName]: inputValue });

    }
    /* get data  */
    componentDidMount() {

        /* get leave type data  */

        // axios.get("http://localhost/idb_laravel/my_project/api/photos")  
        axios.get("https://react.mamunforever.com/api/leave_type_get.php")
            .then(response => {
                console.log(response.data)
                console.log(response.employee_id)
                /*   console.log(response.data)
                 console.log(response.status) */
                this.setState({ leave_type: response.data })

            })
            .catch(error => {
                console.log(error)
            })

        /* manage leave list*/
        axios.get("https://react.mamunforever.com/api/leave_get.php")
            .then(response => {
                console.log(response.data)
                console.log(response.employee_id)
                /*   console.log(response.data)
                 console.log(response.status) */
                this.setState({ leave: response.data })

            })
            .catch(error => {
                console.log(error)
            })


        /* get employee data */
        axios.get("https://react.mamunforever.com/api/employee/employee_get.php")
            .then(response => {
                console.log(response.data)

                /*   console.log(response.data)
                 console.log(response.status) */

                this.setState({ employee: response.data })
            })
            .catch(error => {
                console.log(error)
            })
    }


    handleSubmit = event => {

        event.preventDefault();




        //axios.get(`http://localhost/api/api.php`)
        axios.post(`https://react.mamunforever.com/api/leave_post.php`, this.state)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {
                console.log(response)
                console.log(response.data)
                this.filter = response.data

            })
            .catch(e => {
                //this.errors.push(e)
            })
    }



    render() {

        var employee_id = this.state.employee.map((edata) => {

            return <option value={edata.id}>{edata.name}</option>
        })

        var leave_type = this.state.leave_type.map((edata) => {

            return <option value={edata.id}>{edata.name}</option>
        })
const list_status=[
    {
        id:"",
        name:"Select"

    },
    {
        id:"0",
        name:"Pending"

    },
    {
        id:"1",
        name:"Aproval"

    },
    {
        id:"2",
        name:"Reject"

    }
]
const list=list_status.map((data)=>{

return <option value={data.id}>{data.name}</option>
})
        const my = this.state.leave.map((data) => {
            return <tr>
                <td>
                    {data.id}
                </td>
                <td>
                    {data.employee_id}
                </td>
                <td>
                    {data.leave_type_id}
                </td>
                <td>
                    {data.start_date}
                </td>
                <td>
                    {data.end_date}
                </td>
                <td>
                    {data.reason}
                </td>
                <td>
                    {data.remark}

                </td>
                <td>
                    {this.stattement(data.status)}
                    {/* if({data.status}==0){
                        <span class="label label-primary">Pending</span>
                    }else{onClick={(e)=>this.handleDelete(e,item.id)}
                        <span class="label label-primary">aprove</span>
                    } */}


                </td>
                <td>
                    <select onChange={(e)=>this.handleStatus(e,data.id)}>                    
                      {list}
                    </select>
                </td>
                <td class="text-right">
                    <div class="btn-group">
                        <button class="btn-white btn btn-xs">{this.state.show}</button>
                        <button class="btn-white btn btn-xs">Edit</button>
                    </div>
                </td>
            </tr>


        })
        return (
            <>

                <div id="wrapper">
                    <SidebarMenu />
                    <div id="page-wrapper" class="gray-bg dashbard-1">
                        <Topbar />
                        <div class="row wrapper border-bottom white-bg page-heading">
                            <div class="col-lg-10">
        <h2>Overtime {this.handleStatus }</h2>
                                <ol class="breadcrumb">
                                    <li class="breadcrumb-item">
                                        <a href="index.html">Home</a>
                                    </li>

                                    <li class="breadcrumb-item active">
                                        <strong>Overtime</strong>
                                    </li>
                                </ol>
                            </div>
                            <div class="col-lg-2">

                            </div>
                        </div>

                        <div class="wrapper wrapper-content animated fadeInRight ecommerce">


                            <div class="ibox-content m-b-sm border-bottom">
                                <div class="row">
                                    <div class="col-sm-4">
                                        <div class="form-group">
                                            <label class="col-form-label" for="product_name">Employee Name</label>
                                            <input type="text" id="product_name" name="product_name" value="" placeholder="Product Name" class="form-control" />
                                        </div>
                                    </div>
                                    <div class="col-sm-4">
                                        <div class="form-group">
                                            <label class="col-form-label" for="price">date</label>
                                            <input type="text" id="price" name="price" value="" placeholder="Price" class="form-control" />
                                        </div>
                                    </div>

                                    <div class="col-sm-4">
                                        <div class="form-group">
                                            {/*  <label class="col-form-label" for="status">Status</label>
            <select name="status" id="status" class="form-control">
                <option value="1" selected>Enabled</option>
                <option value="0">Disabled</option>
            </select> */}
                                            <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#modalCart">Launch modal</button>
                                        </div>
                                    </div>
                                </div>

                            </div>

                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="ibox">
                                        <div class="ibox-content">

                                            <table class="footable table table-stripped toggle-arrow-tiny" data-page-size="15">
                                                <thead>
                                                    <tr>

                                                        <th data-toggle="true">Id</th>
                                                        <th data-hide="phone">Employee Name</th>
                                                        <th data-hide="all">leave type</th>
                                                        <th data-hide="all">start date</th>
                                                        <th data-hide="all">end date</th>
                                                        <th data-hide="all">reason</th>
                                                        <th data-hide="all">remark</th>

                                                        <th data-hide="phone">Status</th>
                                                        <th class="text-right" data-sort-ignore="true">Action</th>

                                                    </tr>
                                                </thead>
                                                <tbody>

                                                    {my}

                                                </tbody>


                                                <tfoot>
                                                    <tr>
                                                        <td colspan="6">
                                                            <ul class="pagination float-right"></ul>
                                                        </td>
                                                    </tr>
                                                </tfoot>
                                            </table>

                                        </div>
                                    </div>
                                </div>
                            </div>


                        </div>



                        {/* modal  */}
                        {/*  <!-- Button trigger modal--> */}


                        {/* <!-- Modal: modalCart --> */}
                        <div class="modal fade" id="modalCart" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
                            aria-hidden="true">
                            <div class="modal-dialog" role="document">
                                <div class="modal-content">
                                    {/*   <!--Header--> */}
                                    <div class="modal-header">
                                        <h4 class="modal-title" id="myModalLabel">Your cart</h4>
                                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                            <span aria-hidden="true">×</span>
                                        </button>
                                    </div>
                                    {/*  <!--Body--> */}
                                    <div class="modal-body">
                                        <form onSubmit={this.handleSubmit}>
                                            <div class="form-group">
                                                <label for="employee">Employee Name</label>
                                                {/*     <input type="text" class="form-control" id="employee" placeholder="Example input"/> */}
                                                <select class="form-control" onChange={this.handleChange} name="employee_id">
                                                    <option>
                                                        select employee
               </option>

                                                    {employee_id}

                                                </select>

                                            </div>
                                            <div class="form-group">
                                                <label for="employee">leave type</label>

                                                <select class="form-control" onChange={this.handleChange} name="leave_type_id">
                                                    <option>
                                                        select employee
               </option>

                                                    {leave_type}

                                                </select>
                                            </div>


                                            <div class="form-group">

                                                <label for="start_date">start_date</label>
                                                <input type="date" class="form-control" onChange={this.handleChange} name="start_date" id="start_date" />
                                            </div>
                                            <div class="form-group">

                                                <label for="end_date">end_date</label>
                                                <input type="date" class="form-control" id="end_date" onChange={this.handleChange} name="end_date" placeholder="Hours" />
                                            </div>
                                            <div class="form-group">

                                                <label for="formGroupExampleInput">Leave Reason</label>
                                                <textarea type="text" class="form-control" onChange={this.handleChange} name="leave_reason" placeholder="description" />
                                            </div>
                                            <div class="form-group">

                                                <label for="formGroupExampleInput">remark</label>
                                                <textarea type="text" class="form-control" onChange={this.handleChange} name="remark" placeholder="description" />
                                            </div>


                                            <div class="modal-footer">
                                                <button type="button" class="btn btn-outline-primary" data-dismiss="modal">Close</button>
                                                <button type="submit" class="btn btn-primary" onClick={this.refreshPage}>send</button>
                                            </div>
                                        </form>
                                    </div>

                                    {/*  <!--Footer--> */}

                                </div>
                            </div>
                        </div>
                        {/* <!-- Modal: modalCart --> */}


                        <Footer />
                    </div>
                    <Smallchat />
                    <TopbarNotice />
                </div>


            </>
        )
    }
}
export default Overtime;