import React, { Component } from 'react';
import SidebarMenu from '../component/SidebarMenu';
import Footer from '../component/Footer';
import Topbar from '../component/Topbar';
import Content from '../component/Content';
import Smallchat from '../component/Smallchat';
import TopbarNotice from '../component/TopbarNotice';
import axios from 'axios';
class Users extends React.Component {

    constructor() {
        super();
        this.state = {
            id:"",
            name: "",
            email: "",
            password: "",
            role_id: "",
            images:"",
            role: [],
            users: []
        }
    }
    /*   refreshPage(){
            window.location.reload(true);
        }  */
    handleChange = event => {
        var inputName = event.target.name;
        var inputValue = event.target.value;
        this.setState({ [inputName]: inputValue });
       

    }
    /* get data  */
    componentDidMount() {

   

        // axios.get("http://localhost/idb_laravel/my_project/api/photos")  
        axios.get("https://react.mamunforever.com/api/role_get.php")
            .then(response => {

            //    console.log(response.data)  
                this.setState({ role: response.data })

            })
            .catch(error => {
                console.log(error)
            })


        axios.get("https://react.mamunforever.com/api/users_get.php")
            .then(response => {
               // console.log(response.data)

                this.setState({ users: response.data })

            })
            .catch(error => {
                console.log(error)
            })



    }
  
    getPhoto=event=>{
        const img=event.target.files[0].name;
        this.setState({ images:img} )
        console.log(event.target.files[0]);

    }
    handleEdit=(e,item)=>{
       console.log(item.name)
       this.setState({id:item.id, name:item.name, email:item.email, password:item.password, role_id:e});
    }
    handleUpdate=e=>{
        e.preventDefault();
        console.log(this.state.id);
        axios.post(`https://react.mamunforever.com/api/users_update.php`, this.state)
        /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
        .then(response => {

            console.log(response.data)
            /* this.filter = response.data
         */
        })
        .catch(e => {
            //this.errors.push(e)
   
        })
    }
    handleDelete=(e,item)=>{
        console.log(item)
        let json={'id':item};
        axios.post(`https://react.mamunforever.com/api/users_delete.php`, json)
        /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
        .then(response => {

            console.log(response.data)
            /* this.filter = response.data
         */
        })
        .catch(e => {
            //this.errors.push(e)
   
        })
    }
    handleSubmit = event => {

        event.preventDefault();

        axios.post(`https://react.mamunforever.com/api/users_post.php`, this.state)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {

                console.log(response.data)
                /* this.filter = response.data
             */
            })
            .catch(e => {
                //this.errors.push(e)
       
            })

  }
    



    render() {

        var role_list = this.state.role.map((data) => {

            return <option value={data.id}>{data.name}</option>
        })

        const users_list = this.state.users.map((data) => {
            return <tr>
                <td>
                    {data.id}
                </td>
                <td>
                    {data.name}
                </td>
                <td>
                    {data.email}
                </td>
                <td>
                    {data.password}
                </td>
                <td>
                    {data.images}
                </td>
                <td>
                    <span class="label label-primary">Enable</span>
                </td>
                <td class="text-right">
                   {/*  <div class="btn-group"> */}
                    <div className="btn-group">
                                <button type="button" onClick={(e)=>this.handleDelete(e,data.id)} className="btn btn-default btn-sm"><i className="fas fa-trash text-danger" ></i></button>
                                <button type="button" onClick={(e)=>this.handleEdit(e,data)}class="close" data-dismiss="modal" aria-label="Close" className="btn btn-default btn-sm"><i className="fas fa-edit text-success" data-toggle="modal" data-target="#modal-user-edit" ></i></button>
                                <button type="button" onClick={(e)=>this.handleView(e,data)} className="btn btn-default btn-sm" data-toggle="modal" data-target="#modal-user-details"><i className="fas fa-folder text-info"></i></button>
 
                              </div>
                      {/*   <button class="btn-white btn btn-xs">View</button>
                        <button class="btn-white btn btn-xs">Edit</button> */}
                   {/*  </div> */}
                </td>
            </tr>


        })
        return (
            <>

                <div id="wrapper">
                    <SidebarMenu />
                    <div id="page-wrapper" class="gray-bg dashbard-1">
                        <Topbar />
                        <div class="row wrapper border-bottom white-bg page-heading">
                            <div class="col-lg-10">
                                <h2>Users</h2>
                                <ol class="breadcrumb">
                                    <li class="breadcrumb-item">
                                        <a href="index.html">Home</a>
                                    </li>

                                    <li class="breadcrumb-item active">
                                        <strong>Users</strong>
                                    </li>
                                </ol>
                            </div>
                            <div class="col-lg-2">

                            </div>
                        </div>

                        <div class="wrapper wrapper-content animated fadeInRight ecommerce">


                            <div class="ibox-content m-b-sm border-bottom">
                                <div class="row">

                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <label class="col-form-label" for="product_name">Search</label>
                                            <input type="text" id="product_name" name="product_name" value="" placeholder="Searching................" class="form-control" />
                                        </div>
                                    </div>

                                    <div class="col-sm-6">
                                        <div class="form-group" style={{ marginTop: "7%", float: "right" }}>
                                            <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#modalCart">Add Users</button>
                                        </div>
                                    </div>
                                </div>

                            </div>

                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="ibox">
                                        <div class="ibox-content">

                                            <table class="footable table table-stripped toggle-arrow-tiny" data-page-size="15">
                                                <thead>
                                                    <tr>

                                                        <th data-toggle="true">Id</th>
                                                        <th data-hide="phone">Name</th>
                                                        <th data-hide="phone">Email</th>
                                                        <th data-hide="phone">Password</th>
                                                        <th data-hide="phone">images</th>


                                                        <th data-hide="phone">Status</th>
                                                        <th class="text-right" data-sort-ignore="true">Action</th>

                                                    </tr>
                                                </thead>
                                                <tbody>

                                                    {users_list}

                                                </tbody>


                                                <tfoot>
                                                    <tr>
                                                        <td colspan="6">
                                                            <ul class="pagination float-right"></ul>
                                                        </td>
                                                    </tr>
                                                </tfoot>
                                            </table>

                                        </div>
                                    </div>
                                </div>
                            </div>


                        </div>



                        {/* modal  */}
                        {/*  <!-- Button trigger modal--> */}


                        {/* <!-- Modal: modalCart --> */}
                        <div class="modal fade" id="modalCart" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
                            aria-hidden="true">
                            <div class="modal-dialog" role="document">
                                <div class="modal-content">
                                    {/*   <!--Header--> */}
                                    <div class="modal-header">
                                        <h4 class="modal-title" id="myModalLabel">Add Users</h4>
                                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                            <span aria-hidden="true">×</span>
                                        </button>
                                    </div>

                                    {/*  <!--Body--> */}
                                    <div class="modal-body">
                                        <form onSubmit={this.handleUpdate} enctype="multipart/form-data">

                                            <div class="form-group">
                                                <label for="name">Name</label>
                                                <input type="text" class="form-control" value={this.state.name || ''} id="name" onChange={this.handleChange} name="name" placeholder="Enter your name" />
                                            </div>
                                            <div class="form-group">
                                                <label for="email">Email</label>
                                                <input type="text" class="form-control" id="email" value={this.state.email || ''}onChange={this.handleChange} name="email" placeholder="Enter your email" />
                                            </div>
                                            <div class="form-group">
                                                <label for="password">Password</label>
                                                <input type="text" class="form-control" id="password" value={this.state.password || ''} onChange={this.handleChange} name="password" placeholder="Enter your password" />
                                            </div>
                                            <div class="form-group">
                                                <label for="images">Images</label>
                                                <input type="file" class="form-control" id="images"   onChange={this.getPhoto} name="images" placeholder="Enter your password" />
                                            </div>
                                            <div class="form-group">
                                                <label for="role_id">Role</label>
                                                {/*     <input type="text" class="form-control" id="employee" placeholder="Example input"/> */}
                                                <select class="form-control" onChange={this.handleChange}  name="role_id">
                                                    <option>
                                                        select role
                                                    </option>

                                                    {role_list}

                                                </select>
                                            </div>
                                            <div class="modal-footer">
                                                <button type="button" class="btn btn-outline-primary" data-dismiss="modal">Close</button>
                                                <button type="submit" class="btn btn-primary" onClick={this.refreshPage}>send</button>
                                            </div>
                                        </form>
                                    </div>

                                    {/*  <!--Footer--> */}

                                </div>
                            </div>
                        </div>
                        {/* <!-- Modal: modalCart --> */}


                        <Footer />
                    </div>
                    <Smallchat />
                    <TopbarNotice />
                </div>


            </>
        )
    }
}
export default Users;