import React, { Component } from 'react';
import SidebarMenu from '../component/SidebarMenu';
import Footer from '../component/Footer';
import Topbar from '../component/Topbar';
import Content from '../component/Content';
import Smallchat from '../component/Smallchat';
import TopbarNotice from '../component/TopbarNotice';
import axios from 'axios';
class Employee_salary_config extends React.Component {

    constructor() {
        super();
        this.state = {
            employee_id: "",
            payroll_type_id: "",
            amount: "",
            emp_id:"",
            employee_salary_config:[],
            payroll_type: [],
            employee: []
        }
    }
    //  refreshPage(){
    // 		window.location.reload(true);
    // 	}
    handleChange = event => {
        var inputName = event.target.name;
        var inputValue = event.target.value;
        this.setState({ [inputName]: inputValue });


      //  console.log(this.state.emp_id)
    

    }

    getemp_salary = event =>{
       let e_id=event.target.value
        console.log(e_id)

                let json={'id':e_id};
            axios.post("https://react.mamunforever.com/api/employee_salary_config_get.php", json)
            .then(response => {
                console.log(response.data)             
                this.setState({employee_salary_config: response.data })

            })
            .catch(error => {
                console.log(error)
            }) 


            
    }
 

    /* get data  */
    componentDidMount() {
       


        /* get timesheet data  */

        // axios.get("http://localhost/idb_laravel/my_project/api/photos")  
        axios.get("https://react.mamunforever.com/api/payroll_type_get.php")
            .then(response => {
                console.log(response.data)             
                this.setState({ payroll_type: response.data })

            })
            .catch(error => {
                console.log(error)
            })


        /* get employee data */
        axios.get("https://react.mamunforever.com/api/employee/employee_get.php")
            .then(response => {
                console.log(response.data)

                /*   console.log(response.data)
                 console.log(response.status) */

                this.setState({ employee: response.data })
            })
            .catch(error => {
                console.log(error)
            })
    }


    handleSubmit = event => {

        event.preventDefault();




        //axios.get(`http://localhost/api/api.php`)
        axios.post(`https://react.mamunforever.com/api/employee_salary_config_post.php`, this.state)
            /*  axios.post(`http://localhost/idb_laravel/my_project/api/photos`, this.state) */
            .then(response => {
                console.log(response)
                console.log(response.data)
                this.filter = response.data

            })
            .catch(e => {
                //this.errors.push(e)
            })
    }



    render() {
      
      /*   let e_id=this.state.emp_id;
        {this.getemp_salary(e_id)} */
        var employee_id = this.state.employee.map((edata) => {

            return <option value={edata.id}>{edata.name}</option>
        })
        var payroll_type_id = this.state.payroll_type.map((data) => {

            return <option value={data.id}>{data.name}</option>
        })

        const my = this.state.employee_salary_config.map((data) => {
            return <tr>
                <td>
                    {data.id}
                </td>
                <td>
                    {data.name}
                </td>
                <td>
                    {data.amount}
                </td>                
                <td>
        <span class="label label-primary">Enable</span>
                </td>
                <td class="text-right">
                    <div class="btn-group">
                        <button class="btn-white btn btn-xs">View</button>
                        <button class="btn-white btn btn-xs">Edit</button>
                    </div>
                </td>
            </tr>


        })
        return (
            <>

                <div id="wrapper">
                    <SidebarMenu />
                    <div id="page-wrapper" class="gray-bg dashbard-1">
                        <Topbar />
                        <div class="row wrapper border-bottom white-bg page-heading">
                            <div class="col-lg-10">
                                <h2>Overtime</h2>
                                <ol class="breadcrumb">
                                    <li class="breadcrumb-item">
                                        <a href="index.html">Home</a>
                                    </li>

                                    <li class="breadcrumb-item active">
                                        <strong>Overtime </strong>
                                    </li>
                                </ol>
                            </div>
                            <div class="col-lg-2">

                            </div>
                        </div>

                        <div class="wrapper wrapper-content animated fadeInRight ecommerce">


                            <div class="ibox-content m-b-sm border-bottom">
                                <div class="row">
                                    <div class="col-sm-4">
                                    <div class="form-group">
                                                <label for="employee">Employee Name</label>
                                                {/*     <input type="text" class="form-control" id="employee" placeholder="Example input"/> */}
                                                <select class="form-control" onChange={(e)=>this.getemp_salary(e)} name="emp_id">
                                                    <option>
                                                        select employee
                                                    </option>

                                                    {employee_id}

                                                </select>
                                            </div>
                                    </div>
                                    <div class="col-sm-4">
                                        <div class="form-group">
                                            <label class="col-form-label" for="price">date</label>
                                            <input type="text" id="price" name="price" value="" placeholder="Price" class="form-control" />
                                        </div>
                                    </div>

                                    <div class="col-sm-4">
                                        <div class="form-group">
                                            {/*  <label class="col-form-label" for="status">Status</label>
                                            <select name="status" id="status" class="form-control">
                                                <option value="1" selected>Enabled</option>
                                                <option value="0">Disabled</option>
                                            </select> 
                                            */}
                                            <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#modalCart">Launch modal</button>
                                        </div>
                                    </div>
                                </div>

                            </div>

                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="ibox">
                                        <div class="ibox-content">

                                            <table class="footable table table-stripped toggle-arrow-tiny" data-page-size="15">
                                                <thead>
                                                    <tr>

                                                        <th data-toggle="true">Id</th>
                                                        <th data-hide="phone">Name</th>
                                                        <th data-hide="all">amount</th>                                                     
                                                        <th class="text-right" data-sort-ignore="true">Action</th>

                                                    </tr>
                                                </thead>
                                                <tbody>                                                  
                                                    {my}
                                                </tbody>


                                                <tfoot>
                                                    <tr>
                                                        <td colspan="6">
                                                            <ul class="pagination float-right"></ul>
                                                        </td>
                                                    </tr>
                                                </tfoot>
                                            </table>

                                        </div>
                                    </div>
                                </div>
                            </div>


                        </div>



                        {/* modal  */}
                        {/*  <!-- Button trigger modal--> */}


                        {/* <!-- Modal: modalCart --> */}
                        <div class="modal fade" id="modalCart" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
                            aria-hidden="true">
                            <div class="modal-dialog" role="document">
                                <div class="modal-content">
                                    {/*   <!--Header--> */}
                                    <div class="modal-header">
                                        <h4 class="modal-title" id="myModalLabel">Your cart</h4>
                                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                            <span aria-hidden="true">×</span>
                                        </button>
                                    </div>
                                    {/*  <!--Body--> */}
                                    <div class="modal-body">
                                        <form onSubmit={this.handleSubmit}>
                                            <div class="form-group">
                                                <label for="employee">Employee Name</label>
                                                {/*     <input type="text" class="form-control" id="employee" placeholder="Example input"/> */}
                                                <select class="form-control" onChange={this.handleChange} name="employee_id">
                                                    <option>
                                                        select employee
                                                    </option>

                                                    {employee_id}

                                                </select>
                                            </div>
                                            <div class="form-group">
                                                <label for="employee">Payroll Type</label>
                                                {/*     <input type="text" class="form-control" id="employee" placeholder="Example input"/> */}
                                                <select class="form-control" onChange={this.handleChange} name="payroll_type_id">
                                                    <option>
                                                        select payroll type
                                                    </option>

                                                    {payroll_type_id}

                                                </select>
                                            </div>

                                           
                                            <div class="form-group">

                                                <label for="amount">Amount</label>
                                                <input type="text" class="form-control" id="amount" onChange={this.handleChange} name="amount" placeholder="Amount" />
                                            </div>                                         
                                            <div class="modal-footer">
                                                <button type="button" class="btn btn-outline-primary" data-dismiss="modal">Close</button>
                                                <button type="submit" class="btn btn-primary" onClick={this.refreshPage}>send</button>
                                            </div>
                                        </form>
                                    </div>

                                    {/*  <!--Footer--> */}

                                </div>
                            </div>
                        </div>
                        {/* <!-- Modal: modalCart --> */}


                        <Footer />
                    </div>
                    <Smallchat />
                    <TopbarNotice />
                </div>


            </>
        )
    }
}
export default Employee_salary_config;