import React,{Component} from 'react';
//import Header from '../component/Header'; 
//import Footer from '../component/Footer'; 
class Login extends React.Component{
  
      constructor(props){
          super(props);
          this.state={username:'',password:''}
      }
      handleInputChange=(e)=>{
          this.setState({[e.target.name]:e.target.value});
      }

      handleLogin=(e)=>{
          e.preventDefault();
          console.log(this.state.username+","+this.state.password)
          if("admin"== this.state.username && "123456"==this.state.password){
              sessionStorage.setItem("username",this.state.username);
              sessionStorage.setItem("password",this.state.password);
            window.location="/home";
        }else{

            alert("invalid username and password");
        }
      }
    
      render(){
        return(
            <>
          

<div class="middle-box text-center loginscreen animated fadeInDown">
<div>
    <div>

        <h1 class="logo-name">HR</h1>

    </div>
    <h3>Welcome to HRM</h3>
  
    <p>Login in. To see it in action.</p>
    <form class="m-t" role="form" onSubmit={this.handleLogin}>
        <div class="form-group">
            <input type="text" class="form-control" onChange={this.handleInputChange}  name="username"placeholder="admin" required=""/>
        </div>
        <div class="form-group">
            <input type="password" class="form-control" onChange={this.handleInputChange}  name="password"placeholder="123456" required=""/>
        </div>
        <button type="submit" class="btn btn-primary block full-width m-b">Login</button>

        <a href="#"><small>Forgot password?</small></a>
        <p class="text-muted text-center"><small>Do not have an account?</small></p>
        <a class="btn btn-sm btn-white btn-block" href="register.html">Create an account</a>
    </form>
    {/* <p class="m-t"> <small>Inspinia we app framework base on Bootstrap 3 &copy; 2014</small> </p> */}
</div>
</div>


</>
            )
        }
    }
    export default Login;