import React,{component} from 'react';
import './Card.css';
class Card extends React.Component{
constructor(props){
super(props);

}
render(){
    return(
        <>
       
           <div className="card">
          
             <div className="card-body">
              <img src={this.props.src} style={{objectFit:"cover",height:"200px"}} width="100%"/>
              <div className="card-title">
                 <h4>{this.props.title}</h4>
              </div>
             <div>{this.props.children}</div>
          </div>
      </div>
        </>

    )
}


}
export default Card;



